@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200&display=swap');
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
img{
    border:none;
}
ol,
ul{
    list-style-type: none;
}
body{
    font-family: "Nunito", cursive;
    font-size: 14px;
    color:rgb(46, 108, 190);
    line-height: 1.8;
}
@media (min-width:451px){
    body{
        font-size: 18px;
    }
}
@media (min-width:769px){
    body{
        font-size: 20px;
    }
}
@media (min-width:1025px){
    body{
        font-size: 26px;
    }
}
.App{
    position: relative;
    width: 100vw;
    height:100vh;
    background-image:url(/dailydog/static/media/background2.b4736575.jpeg);
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
}

.menu-container{
    display: none;
    position: fixed;
    top: 0;
    width: 100vw;
    height: 100vh;
    z-index: 1;
    background: rgba(4,37,410.85);
    color: gold;
    
}
.menu-logo{
    width: 150px;
    margin: 7%auto 0 auto;
    text-align: center;
}
.menu-logo-img{
    width: 100%;
    display:block;
    box-sizing: border-box;
}
.menu-title{
    font-size: 15px;
    text-align: center;
    margin: 7% auto;
}
.menu-text{
font-size: 14px;
text-align: center;
margin: 7% auto;
position: static;
transition: all 0.5s;


}
.menu-button {
    background-color: rgba(255,255,255,0);
    text-align: center;
    margin: 0 auto;
    display: block;
    font-size: 18px;
    color:#0a6e50;
    cursor: pointer;
    transition: all 0.5s;
    padding:0%;
    text-decoration: none;
    
  }
  .menu-button:hover{
    color:blue;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}


.menu-essentialfoods,
.menu-instagram{
    text-align: center;
    margin: 0 auto;
    display: block;
    font-size: 8px;
    text-decoration: none;
    color:#0a6e50;
    cursor: pointer;
    transition: all 0.5s;
}
.menu-essentialfoods:hover,
.menu-instagram:hover{
    color: blue;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.menu-container.opened{
    display: block;
}
.burger{
    position:fixed;
    top: 5px;
    right: 5px;
    width: 30px;
    height: 30px;
    z-index: 2;
    display: block;
    cursor: pointer;
    border: 1px solid #d4bf91;
    border-radius: 5px;
    outline: inherit;
    background-color: white;
}
.lines{
position: absolute;
top: 50%;
left: 50%;
-webkit-transform: translate3d(-50%, -50%, 0);
        transform: translate3d(-50%, -50%, 0);
}
.line{
    width: 15px;
    height: 1px;
    display: block;
    background-color: #424242;
    margin: 3px auto;
    transition: all 0.3s easte-in-out;;
}
.burger.is-active.line:nth-child(2){
    opacity: 0;
}
.burger.burger.is-active.line:nth-child(1){
    -webkit-transform:translateY(4px)rotate(45deg);
            transform:translateY(4px)rotate(45deg);
}
.burger.is-active.line:nth-child(3){
    -webkit-transform:translateY(-4px)rotate(-45deg);
            transform:translateY(-4px)rotate(-45deg);

}
@media/*dekstop*/(min-width:769px){
    .menu-container{
        display: block;
        position: absolute;
        height: 70%;
        width: 20%;
        left: 4%;
        top: 15%;
        background: rgba(4, 32, 41, 0.35);
    }
    .burger{
        display: none;
    }
    .menu-logo{
        width: 50%
    }
    .menu-essentialfoods,
    .menu-instagram{
        position: absolute;
        margin-left: 12%;
    }
    .menu-essentialfoods{
        bottom: 14%;
    }
    .menu-instagram{
        bottom: 7%;
    }  
}
@media/*XL desktop*/(min-width: 1025px){
    .menu-title{
        font-size: 30px;
    }
    .menu-essentialfoods,
    .menu-instagram{
        font-size: 14px;
    }
}


